<template>
    <div class="content">
        <div class="search">
            <input @input="bindKeyInput" type="text" placeholder="搜索歌曲" class="inputSearch">
            <i @click="searchI" style="background:url(data:image/svg+xml;base64,); background-size:56rpx 56rpx; width:56rpx;height:56rpx;"></i>
        </div>
        <div style="margin-top:20rpx;display:flex;flex-direction: column;border-bottom:1px solid #efefef">
            <p class="description">介绍: {{description}}</p>
            <div class="playItem">
                <p class="playCount">播放: {{playCount}}</p>
                <p class="buttonList" @click="buttonListPlay">加入播放列表</p>
            </div>
        </div>
        <div style="margin-top:20rpx;">
            <div class="Single">
                <div v-for="item in songItem" :key="item.id" class="SingleWidth">
                    <div @click="musicUrl(item.id)">
                        <img :src="item.album.picUrl" alt="">
                        <div>
                            <p class="name">{{item.name}}</p>
                            <div class="people">
                                <p v-for="(itempeo,indexa) in item.album.artists" :key="itempeo.id" v-if="indexa<=1">
                                    {{itempeo.name}} &nbsp;
                                </p>
                            </div>
                        </div>
                    </div>
                    <i @click="JoinTheList(item.id)" style="background:url(data:image/svg+xml;base64,); background-size:40rpx 40rpx; width:40rpx;height:40rpx;"></i>
                </div>
            </div>
        </div>
        <div class="Playing" @click="listenerButtonPause">
            <div class='wrap'>
                <i style="background:url(data:image/svg+xml;base64,); background-size:80rpx 80rpx; width:80rpx;height:80rpx;margin-top:20rpx;margin-left:20rpx"></i>
            </div>

        </div>
    </div>
</template>

<script>
var Fly = require("flyio/dist/npm/wx"); //npm引入方式
var fly = new Fly(); //创建fly实例
export default {
    data() {
        return {
            songItem: {},
            playCount: undefined,
            description: undefined
        };
    },
    onLoad: function(options) {
        let songItemUrl =
            "http://localhost:3000/playlist/detail?id=" + options.id;
        fly
            .get(songItemUrl, {})
            .then(d => {
                this.songItem = d.data.result.tracks;
                let playCount = d.data.result.playCount;
                this.description = d.data.result.description;
                let songItemName = d.data.result.name;

                if (playCount > 9999) {
                    playCount = (Math.floor(playCount/1000)/10) + '万'
                }else{
                    playCount = playCount
                }

                this.playCount = playCount

                wx.setNavigationBarTitle({
                    title: songItemName //页面标题为路由参数
                });
            })
            .catch(err => {
                console.log(err.status, err.message);
            });
    },

    mounted() {},
    methods: {
        bindKeyInput: function(e) {
            this.bindKeyInput = e.target.value;
        },
        searchI: function() {
            let searchValue = this.bindKeyInput;

            const url = "../search/main?search=" + searchValue;
            wx.navigateTo({ url });
        },
        musicUrl(e) {
            const url = "../music/main?id=" + e;
            wx.navigateTo({ url });
        },
        //监听button暂停按钮
        listenerButtonPause: function() {
            let e = wx.getStorageSync("musicId");

            if (!e) {
            } else {
                const url = "../music/main?id=" + e;
                wx.navigateTo({ url });
            }
        },
        JoinTheList: function(e) {
            let chongfu = [];
            let playing = wx.getStorageSync("playing");
            for (let index = 0; index < playing.length; index++) {
                const element = playing[index].id;
                chongfu.push(element);
            }
            for (let index = 0; index < chongfu.length; index++) {
                const elementq = chongfu[index];
                if (elementq == e) {
                    var panduanchongfu = true;
                }
            }
            if (!panduanchongfu) {
                let status = {
                    id: e,
                    status: false
                };
                playing.push(status);
                wx.setStorageSync("playing", playing);
            }
        },
        buttonListPlay: function() {
            let playing = []
            wx.setStorageSync("playing",[]);
            for (let index = 0; index < this.songItem.length; index++) {
                const element = this.songItem[index].id;

                let status = {
                    id: element,
                    status: false
                };

                playing.unshift(status);

                
            }

            wx.setStorageSync("playing", playing);



            const url = "../music/main?id=" + this.songItem[0].id;
            wx.navigateTo({ url });

                
        }
    }
};
</script>

<style scoped>
.content {
    display: flex;
    flex-direction: column;
    background: #fff;
    padding: 0 40rpx;
}
.search {
    display: flex;
}
.inputSearch {
    flex: 1;
    margin-right: 20rpx;
    border-radius: 60rpx;
    border: 1px solid #7054dd;
    font-size: 24rpx;
    padding-left: 20rpx;
}
.Single {
    margin-top: 30rpx;
}
.SingleWidth {
    width: 670rpx;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.SingleWidth > div {
    display: flex;
    margin-bottom: 30rpx;
}
.SingleWidth > div img {
    width: 120rpx;
    height: 120rpx;
    border-radius: 10rpx;
}
.SingleWidth > div > div {
    margin-left: 20rpx;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.name {
    font-size: 32rpx;
}
.people {
    margin-top: 10rpx;
    font-size: 24rpx;
    display: flex;
    color: #999;
    max-width: 420rpx;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.Playing {
    position: fixed;
    bottom: 20rpx;
    left: 311rpx;

    width: 120rpx;
    height: 120rpx;
    background: #fff;
    border: 2px solid #7054dd;
    border-radius: 50%;
    overflow: hidden;
}
.description {
    font-size: 24rpx;
    line-height: 1.25;
    max-height: 120rpx;
    overflow: hidden;
}
.playItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20rpx
}
.playCount {
    flex: 1;
    text-align: center;
    height: 80rpx;
    background: #f3fbfd;
    line-height: 80rpx;
    color: #333;
    font-size:28rpx;

}
.buttonList {
    flex: 1;
    text-align: center;
    height: 80rpx;
    background: #7054dd;
    line-height: 80rpx;
    color: #fff;
    font-size:28rpx;

}
</style>



